<template>
  <div class="container">  
    <div class="li-logo"></div>
    <div class="li-left"></div>
    <div class="li-middle">
      <div class="li-header"></div>
      <div class="li-form">
        <!--<input class="user-name" type="text" placeholder="User name" v-model="userName" maxlength="50">-->  
        <div class="user-pop-up" id = "user-pop-up"></div> 
        <input class="user-mail" type="text" placeholder="Email" id="iu-emailFriend" v-model="userEmail" maxlength="50" pattern="[a-zA-Z0-9!#$%&amp;'*+\/=?^_`{|}~.-]+@gmail.com" @keyup.enter="logInUser">
        <button class="logIn" @click="logInUser" >Log In</button> 
      </div>          
    </div>
    <div class="li-right"></div> 
  </div>
</template>

<script>
import UserService from './userService.js';
export default {
  data () {
    return {
     userName: '',
     userEmail: '',
    }
  },
  methods: {
    logInUser() {  
      if(document.getElementById("iu-emailFriend").validity.valid && this.userEmail!= "") {  
        var userObject = {
          Name: this.userName,
          Email: this.userEmail,
        }
        this.$http({
          url: `http://172.20.56.12:2399/api/Users`,
          method: 'post',
          body: userObject,
          headers: {                    
            'Content-Type' : 'application/json'          
          }
        })
        .then(response => {      
          UserService.setInfo(response.body);  
          window.location.href = "http://172.20.56.12:8081/#/MainPage";
          var logo = document.getElementById("logo");
          logo.style.display = "block";
          var profile = document.getElementById("profile");
          profile.style.display = "block";
          var notifications = document.getElementById("notifications");
          notifications.style.display = "block";
          var signout = document.getElementById("menu");
          signout.style.display = "block";
          this.userName = response.body.Email.split("@")[0];
        })
        .catch(function (err) {
          console.log(err)        
        });
      }
      else {
        var element = document.getElementById("user-pop-up");
        element.innerHTML = "Invalid e-mail, use a google account";
        element.style.color = "red";
        setTimeout(function(){ element.style.color = "#F1F2F1"; }, 2000);
      }     
    }
  },
  mounted () {
    var logo = document.getElementById("logo");
    logo.style.display = "none";
    var profile = document.getElementById("profile");
    profile.style.display = "none";
    var notifications = document.getElementById("notifications");
    notifications.style.display = "none";
    var signout = document.getElementById("menu");
    signout.style.display = "none";
  }
}
</script>

<style scoped>

.container {
  height: 100%;
  width: 100%;
  overflow-y: hidden;
}

.li-logo {
  width: 100%;
  height: 10%;
  float: left;
  margin-left: 35%;
  margin-top: 3px;
}

.li-left {
  height: 85%;
  width: 30%;
  float: left;
}

.li-middle {
  height: 70%;
  width: 40%;
  float: left;
}

.li-right {
  height: 85%;
  width: 30%;
  float: left;
}

.li-header {
  height: 15%;
  width: 75%;
  float: left;
  margin-top: 18%;
  margin-left: 12%;
  background: url('../assets/chauka.png');
  background-size: 100% 100%;
}

.li-form {
  height: 70%;
  width: 100%;
  float: left;
}

.user-mail {
  position: absolute;
  height: 5%;
  width: 20%;
  top: 50%;
  left: 40%;
}

.user-pop-up {
  position: absolute;
  height: 5%;
  width: 20%;
  top: 40%;
  left: 40%;
}

.logIn {
  position: absolute;
  height: 5%;
  width: 20%;
  top: 60%;
  left: 40%;
  font-size: 20px;
  border-radius: 8px;
}

.logIn:hover {
  cursor: pointer;
  background-color: #CD6427;
  border: 1px solid black;
  color: white;
}
</style>



